/**
 * Main JavaScript Entry Point
 *
 * This file serves as the main entry point for the F2I website JavaScript.
 * It imports and initializes core modules and functionality used across the site.
 */

// Import core components
import Navigation from './components/navigation';
import Accordion from './components/accordion';
import Testimonials from './components/testimonials';
import ProgressBar from './components/progress-bar';
import ImageOptimizer from './components/image-optimizer';

// Import utilities
import { initWebpDetection } from './utils/webp-detection';

/**
 * Initialize all components when DOM is ready
 */
document.addEventListener('DOMContentLoaded', function() {
  console.log('F2I JavaScript initialized');

  // Initialize WebP detection
  initWebpDetection();

  // Initialize core components
  Navigation.init();
  Accordion.init();
  Testimonials.init();
  ProgressBar.init();
  ImageOptimizer.init();
});

/**
 * Note: For now, this file doesn't do anything functional
 * It's just a placeholder for our new architecture
 * We'll gradually move functionality from the existing JavaScript files
 * to these modular components.
 */
